.App {
    box-shadow: 0 6px 50px var(--box-shadow);
    position: relative;
    border-radius: 20px;
    padding: 12px;
    max-width: 600px;
    width: 100%;
    height: 90vh;
    display: flex;
    gap: 10px;
    background-color: var(--color-1);
    flex-direction: column;
}

body input {
    outline: none !important;
}

.settingsBox__themeChangeButton i {
    display: block;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    background-color: var(--color-3);
    border-radius: 100%;
}



.settingsBox__themeChangeButton.theme_open button._black {
    left: -100%;
    background: linear-gradient(90deg, rgb(70, 70, 70) 50%, black 50%);
    transition: 300ms;
}

.settingsBox__themeChangeButton.theme_open button._blueBlack {
    left: -210%;
    background: linear-gradient(90deg, #00a9bf 50%, rgb(63, 63, 63) 50%);
    transition: 400ms;
}

.settingsBox__themeChangeButton.theme_open button._grey {
    left: -320%;
    background: linear-gradient(90deg, #9f9f9f 50%, #525252 50%);
    transition: 500ms;
}

.settingsBox__themeChangeButton.theme_open button._purple {
    left: -430%;
    background: linear-gradient(90deg, #955075 50%, #60253f 50%);
    transition: 600ms;
}

.settingsBox__themeChangeButton.theme_open button._white {
    left: -540%;
    background: linear-gradient(90deg, #ffffff 50%, rgb(59, 59, 59) 50%);
    transition: 700ms;
}

.settingsBox__themeChangeButton.theme_open button._gamingRed {
    left: -650%;
    background: linear-gradient(90deg, #a30000, #300000);
    transition: 700ms;
}



.settingsBox__themeChangeButton button {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: -1;
    transition: 600ms;
    width: 32px;
    background: rgba(0, 0, 0, 0.3);
    height: 32px;
    padding: 0;
    margin: 0;
    border: 0;
    border-radius: 100%;
    transform: translate(-50%, -50%);
}

.App>* {
    font-family: 'Rubik', sans-serif;
}

h1 {
    text-align: center;
    margin: 0;
    color: var(--h1-color);
}

.App__control-center {
    display: flex;
    width: 100%;
}

.App__control-center button {
    width: 50px;
    height: 50px;
    border: 0;
    background-color: var(--color-2);
    border-radius: 0 20px 20px 0;
}

.App__control-center i {
    pointer-events: none;
}

.App__control-center input {
    border: 1px solid var(--color-2);
    padding-left: 12px;
    font-size: 1em;
    flex-grow: 1;
    transition: 400ms;
    border-radius: 20px 0 0 20px;
    background-color: var(--color-0);
}

i {
    color: var(--text-color);
}

.App__control-center input:focus-visible {
    outline: 0;
    background-color: var(--text-color);
}

.App__task-list {
    padding: 10px 0;
    padding-right: 4px;
    background: transparent;
    border-bottom: 1px dotted var(--text-color);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    gap: 8px;
    flex-grow: 1;
    overflow-y: auto;
}

.task-item {
    background-color: var(--color-2);
    border-radius: 16px;
    padding: 4px 12px;
    display: flex;
    align-items: center;
    gap: 6px;
}

.task-item i {
    pointer-events: none;
}

.task-item input {
    background-color: var(--color-2);
    border: 0;
    width: 100%;
    font-size: 16px;
    text-align: center;
    color: var(--text-color);
}

.task-item button {
    width: 24px;
    cursor: pointer;
    height: 24px;
    background: 0;
    border-radius: 50%;
}

.task-item button.rename {
    border: 0;
    font-size: 1.4em;
}

.task-item button.check {
    border: 2px solid var(--text-color);
}

.task-item button.close {
    border: 0;
    font-size: 1.4em;
}

.fa-check {
    opacity: 0;
    transition: 0.4s;
}

.fa-check:active {
    opacity: 1;
}


.task-item p {
    text-align: center;
    flex-grow: 1;
    color: var(--text-color);
}

.App__task-list::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background: transparent;
    border-radius: 10px;
}

.App__task-list::-webkit-scrollbar {
    width: 8px;
    background: transparent;
}

.App__task-list::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: var(--color-2);
}

.task_complete {
    opacity: 0.5;
}

.task_complete i.fa-check {
    opacity: 1;
}

.App__settingsBox {
    right: 20px;
    bottom: 30px;
    position: absolute;
}

.settingsBox__button {
    transition: all 0.7s cubic-bezier(0.92, -0.49, 0.7, 1.65);
    border-radius: 100%;
    width: 50px;
    height: 50px;
    position: relative;
    border: 0px;
    background-color: var(--color-4);
    font-size: 20px;
    color: var(--text-color);
    z-index: 15;
}

.App__settingsBox i {
    pointer-events: none;
}

.themeChangeButton_open .childButton {
    top: -80% !important
}

.themeChangeButton_open .settingsBox__button {
    transform: scale(110%);
}

.settingsBox__themeChangeButton {
    padding: 0;
    position: absolute;
    z-index: 12;
    background-color: var(--color-3);
    border-radius: 100%;
    color: var(--text-color);
    width: 36px;
    height: 36px;
    font-size: 15px;
    border: 0;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    transition: all 0.7s cubic-bezier(0.92, -0.49, 0.7, 1.65);
}

.wrapper {
    width: 100%;
    height: 100%;
    position: fixed;
    background-color: var(--wrapper-color);
    z-index: 250000;
    top: 0;
    left: 0;
    display: flex;
    transition: 400ms;
    justify-content: center;
    align-items: center;
}

.hide {
    visibility: hidden;
    opacity: 0;
}

.visible {
    visibility: visible;
    opacity: 1;
}




.group {
    position: relative;
    margin-top: 12px;
}

.group__item p {
    user-select: none;
    pointer-events: none;
    color: var(--text-color);
    margin: 0;
    cursor: pointer;
}

.group__item:nth-child(1) i {
    display: none;
}

.group__list {
    display: flex;
    padding: 12px 12px;
    padding-right: 56px;
    overflow-x: auto;
    overflow-y: hidden;
    border-radius: 14px;
    background-color: var(--color-2);
    justify-content: start;
    align-items: center;
}

.group__item {
    transition: 400ms;
    cursor: pointer;
    display: flex;
    border: var(--color-4) solid 1px;
    border-radius: 6px;
    margin-right: 8px;
    justify-content: center;
    align-items: center;
    padding: 4px 16px;
    gap: 8px;
}

.group__item:hover {
    box-shadow: 0 0 5px 3px var(--box-shadow);
}

.group__item_active {
    transform: scale(105%);
    background-color: var(--color-4);
}

.group__list::-webkit-scrollbar-track {
    background: var(--color-2);
    border-radius: 10px;
}

.group__list::-webkit-scrollbar {
    height: 8px;
    background: var(--color-2);
}

.group__list::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: var(--color-0);
}

.group__add {
    position: absolute;
    transition: 400ms;
    right: 2px;
    top: 11px;
    height: 30.8px;
    cursor: pointer;
    outline: none;
    border: var(--color-4) solid 1px;
    color: var(--color-1);
    font-size: 24px;
    background: var(--text-color);
    width: 30.8px;
    border-radius: 6px;
}

.group__add:hover {
    background-color: var(--color-4);
}

.group__add:active {
    transition: 200ms;
    background-color: var(--color-3);
}








.addGroupBlock {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 20px;
    background-color: var(--color-2);
    padding: 32px;
    width: 90%;
    border-radius: 20px;
    max-width: 320px;
}

.addGroupBlock h3 {
    color: var(--text-color);
    margin: 0;
}

.addGroupBlock input {
    width: 100%;
    height: 3.2vh;
    max-height: 40px;
    color: var(--text-color);
    border: var(--color-4) 2px solid;
    background-color: var(--color-3);
    border-radius: 6px;
}

.addGroupBlock button {
    width: 100%;
    height: 3.2vh;
    max-height: 40px;
    color: var(--text-color);
    border: var(--color-4) 2px solid;
    background-color: var(--color-3);
    border-radius: 6px;
}


.renameBlock {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 20px;
    background-color: var(--color-2);
    padding: 32px;
    width: 90%;
    border-radius: 20px;
    max-width: 320px;
}

.renameBlock h3 {
    color: var(--text-color);
    margin: 0;
}

.group__phoneButton,
.group__activeGroupNow {
    visibility: hidden;
    opacity: 0;
}

.renameBlock input {
    width: 100%;
    height: 3.2vh;
    max-height: 40px;
    color: var(--text-color);
    border: var(--color-4) 2px solid;
    background-color: var(--color-3);
    border-radius: 6px;
}

.renameBlock button {
    width: 100%;
    height: 3.2vh;
    max-height: 40px;
    color: var(--text-color);
    border: var(--color-4) 2px solid;
    background-color: var(--color-3);
    border-radius: 6px;
}



@media (max-width: 575px) {
    .group__list {
        flex-direction: column;
        align-items: stretch;
        background: transparent;
        padding: 12px 0 12px 0;
        overflow: visible;
        width: 100%;
        gap: 14px;
    }

    .group {
        padding: 6px 32px 40px 32px;
        border-radius: 10px;
        background-color: var(--color-2);
        height: 74px;
        overflow: hidden;
    }

    .group__phoneButton {
        visibility: visible;
        opacity: 1;
        background-color: var(--box-shadow);
        color: var(--text-color);
        height: 30px;
        width: 100%;
        border-radius: 6px;
        margin: auto;
        display: flex;
        font-size: 24px;
        justify-content: center;
        align-items: center;
    }

    .group__item {
        width: 100%;
    }

    .group__add {
        top: 100%;
        opacity: 0;
        visibility: hidden;
        right: 50%;
        transform: translate(50%, -130%);
        width: calc(100% - 64px);
    }

    .group__activeGroupNow {
        visibility: visible;
        opacity: 1;
        color: var(--text-color);
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}